import { composeWithDevTools } from "redux-devtools-extension";
const initialState = {
  fav : [],
  collect : []
}
export const Reducer = (state = initialState, action) => {
  console.log("data", state, "action", action.val);
  switch (action.type) {
    case "add_Fav":
      return { ...state, fav: [...state.fav , { image: action.val }] }
    case "add_Collect":
      return { ...state, collect: [...state.collect , { image: action.val }] }
    case "removeFav":
      var data = state.fav.filter(st => st.image.id != action.val)
      return { ...state, fav: data }
    case "removeCollect":
      var data = state.collect.filter(st => st.image.id != action.val)
      return { ...state, collect: data }
  }
};


